const LoginService = {
  login(userInfo:any) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        if (userInfo.name === "") {
          resolve({id: 123, name: "omg"});
        } else {
          reject({err: {msg: ""}});
        }
      }, 1000);
    });
  },
  // 
  getMoreUserInfo(userInfo:any) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        if (userInfo.id === 123) {
          resolve({...userInfo, score: "100"});
        } else {
          reject({msg: ""});
        }
      }, 1000);
    });
  },
};
export default LoginService;
